import React, { useEffect } from 'react';

const Scene3: React.FC = () => {

  useEffect(() => {
    const imgs = document.querySelector('img');

    if (imgs) {
      imgs.addEventListener('mouseover', mouseOverHandler, true);
      imgs.addEventListener('mouseleave', mouseLeaveHandler, true);
    }

    return () => {
      if (imgs) {
        imgs.removeEventListener('mouseover', mouseOverHandler, true);
        imgs.removeEventListener('mouseleave', mouseLeaveHandler, true);
      }
    }
  }, []);

  const mouseOverHandler = (e: MouseEvent) => {
    e.preventDefault();
    const img = e.target as HTMLImageElement;

    img.setAttribute('data-src', img.src);
    img.setAttribute('src', img.dataset['mouseover']!);
  };

  const mouseLeaveHandler = (e: MouseEvent) => {
    e.preventDefault();
    const img = e.target as HTMLImageElement;

    img.setAttribute('data-mouseover', img.src);
    img.setAttribute('src', img.dataset['src']!);
  };

  return (
    <main className='Scene3'>
      <img className='icon' src='/assets/imgs/in/refrigerator/strawberry-milk.png' style={{ top: '416px', left: '312px' }} data-mouseover='/assets/imgs/in/mouseover/strawberry-milk.png' />
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/blue-milk.png' style={{ top: '413px', left: '552px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/sprite.png' style={{ top: '546px', left: '320px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/coke.png' style={{ top: '548px', left: '555px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/water.png' style={{ top: '701px', left: '449px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/water.png' style={{ top: '701px', left: '552px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/door.png' style={{ top: '246px', left: '288px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/refrigerator/door.png' style={{ top: '246px', left: '527px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/tteokbokki.png' style={{ top: '207px', left: '1185px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/nemoni.png' style={{ top: '709px', left: '818px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/doong.png' style={{ top: '391px', left: '1196px'}} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/cream-bread.png' style={{ top: '475px', left: '818px' }} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/red-bean-bread.png' style={{ top: '475px', left: '976px'}} onMouseOver={handleMouseOver} />*/}
      {/*<img className='icon' src='/assets/imgs/in/mede/tibisket.png' style={{ top: '304px', left: '849px' }} onMouseOver={handleMouseOver} />*/}

      <style jsx>{`
        .Scene3 {
          display: block;
          position: fixed;
          width: 1920px;
          height: 980px;
          background-image: url('/assets/imgs/in/background.png');
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
        .icon {
          position: absolute;
          width: fit-content;
          heigth: fit-content;
          object-fit: contain;
          object-position: center;
          cursor: pointer;
        }
      `}

      </style>
    </main>
  )
};
      
export default Scene3;
